html, body, ul, li, p, img, h1, h2, h3, h4 {
	margin:0;
	padding:0;
}
ul {
	list-style:none;
}
a {
  text-decoration: none;
  transition:0.5s ease;
}
html {
	background:#fbfbfb;
}
@font-face {
    font-family: 'bebas_neuebold';
    src: url('../font/bebasneue_bold-webfont.eot');
    src: url('../font/bebasneue_bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../font/bebasneue_bold-webfont.woff2') format('woff2'),
         url('../font/bebasneue_bold-webfont.woff') format('woff'),
         url('../font/bebasneue_bold-webfont.ttf') format('truetype'),
         url('../font/bebasneue_bold-webfont.svg#bebas_neuebold') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'HelveticaLTStdLight';
    src: url('../font/HelveticaLTStdLight.eot');
    src: url('../font/HelveticaLTStdLight.eot') format('embedded-opentype'),
         url('../font/HelveticaLTStdLight.woff2') format('woff2'),
         url('../font/HelveticaLTStdLight.woff') format('woff'),
         url('../font/HelveticaLTStdLight.ttf') format('truetype'),
         url('../font/HelveticaLTStdLight.svg#HelveticaLTStdLight') format('svg');
}
$width:1180px;
%width_center {
	width: $width;
	margin: 0 auto;
}
.head {
	background: #272b34;
	background-size: cover;
	text-align: center;

	&_control {
	   position: absolute;
	   width: 550px;
	   margin: 0 auto;
	   left: 50%;
	   margin-left: -275px;
		text-align: left;
		z-index:2;
	}
	&_navigation {
		height:45px;
		background:#23252a;
		border-bottom:2px dashed #37393d;
		box-shadow:0 3px 0 0 #23252a;
		margin-bottom: -53px;
		position: relative;
		z-index: 1;

		&_wraper {
			@extend %width_center;
		}
	}
}
.head_content_more {
	display: inline-block;
	color: #ffebca;
	text-transform: uppercase;
	font: 20px 'Open Sans', sans-serif;
	word-spacing: 3px;
	padding: 10px 60px;
	background: #dd574b;
	border-radius: 5px;
	transition: 0.5s ease;
}
.head_content_more:hover, .atom_more:hover, .portfolio_more:hover, .footer_connect .testimonials_get:hover {
	background:#2190C2;
	transition: 0.5s ease;
}
.head_content h2 {
	color:#c9cbce;
	font: 60px Bebas_neuebold, Arial, sans-serif;
	text-transform:uppercase;
	line-height:1;
	margin: 0 0 13px 0;
}
.head_content h2+p {
	color:#92959a;
	font: 14px 'Open Sans', sans-serif;
	margin: 0 0 31px 0px;
}
.head_navigation_wraper:after {
	width: 100%;
	height: 0;
	visibility: hidden;
	overflow: hidden;
	content: '';
	display: inline-block;
}
.atom_wraper, .portfolio_wraper, .testimonials_wraper{
	width: 1180px;
  margin: 0 auto;
	/*width: 82%;*/
}
.head_navigation_wraper {
	text-align:justify;
}
.head_social {
	display:inline-block;
	line-height: 45px;
	margin: 0 -3px 0 0;
}
.head_social p {
	display:inline-block;
	color:#9d9d9d;
	font: 13px 'Ubuntu Condensed', Tahoma, serif;
	text-transform:uppercase;
	margin: 0 11px 0 0;
}
.head_social ul {
	display: inline-block;
}
.head_social li {
	display:inline-block;
	padding: 0 2px;
}
.head_social li a {
	width:25px;
	height:25px;
	border-radius:50%;
	background:#5c5c5c;
	color:#bcbcbc;
	text-align: center;
    line-height: 25px;
	font-size: 12px;
	transition: 0.5s ease;
}
.head_toogle a:hover, .head_social li a:hover{
    color: #fbfbfb;
	transition: 0.5s ease;
}
.head_toogle {
	display:inline-block;
	margin: 0 0 0 2px;
}
.head_toogle a{
	color:#9d9d9d;
	font: 13px 'Ubuntu Condensed', Tahoma, serif;
	text-transform:uppercase;
	transition: 0.5s ease;
}
.head_toogle_control{
	position:relative;
	left: -125px;
    top: 3px;
	display: inline-block;
	height:15px;
	width: 70px;
	background:#292c31;
	border-radius:3px;
	box-shadow: 1px 1px 1px 0 #393b40, 2px 2px 2px 0 #181a1d inset;
}
#head_toogle_control:after{
	content:'';
	position:absolute;
	height:11px;
	width:25px;
	border-radius:3px;
    top: 2px;
    right: 1px;
	background: rgb(100,100,100) radial-gradient(circle at 50% 0, rgba(255,255,255,0.45), rgba(255,255,255,0.35));
    box-shadow: inset rgba(0,0,0,0.6) 0 -3px 8px, inset rgba(252,255,255,0.7) 0 3px 8px, rgba(0,0,0,0.8) 1px 1px 1px 0px;
}
.head_toogle_control:target:after{
	left: 1px;
	background:#ccc;
}
.head_toogle_website {
	margin: 0 0 0 88px;
}
.head_toogle_website:target{
	right: 1px;
	background:#000;
}
