/***SVG header menu***/
.head_control_bg0{
	fill:#273C53;
}
.head_control_bg1{
	fill:#405D7B;
}
.head_control_menu-bg {
	fill:#E8E8E8;
}
.head_control_text-way {
	fill:none;
}
.head_control_dotted-line{
	fill:none;
	stroke:#5D748C;
	stroke-linecap:square;
	stroke-linejoin:round;
	stroke-miterlimit:10;
	stroke-dasharray:1.9,3.8;
}
.head_control_logo{
	fill:#D3E3F4;
}
.head_control tspan{
	font-size:14px;
}
.head_control text {
	font-size:24px;
}
.head_control_item-active {
	fill:#0E75EA;
}
.head_control text, .head_control tspan {
	font-family:'Verdana';
	fill:#546373;
}
