.header_content_slider_caption {
    position: absolute;
    top: 200px;
    left: 50%;
    width: 650px;
    margin: 0 0 0 -325px;
}
.atom {
	text-align: center;
	margin: 0 0 50px 0;
	background: #fbfbfb;
	padding: 20px 0 0 0;
}
.atom_left_column, .atom_center_column, .atom_right_column {
    display: block;
    float: left;
}
.atom_left_column {
	text-align:left;
	padding: 52px 0 0 0;
	width: 27%;
}
.atom_center_column img{
	width: 100%;
}
.atom_right_column {
	text-align:right;
	padding: 52px 0 0 0;
	width: 27%;
}
.atom_slogan {
    margin: 0 0 35px 0;
}
.atom_slogan h2 {
  font: 80px Bebas_neuebold, Arial, sans-serif;
  color: #4b7db0;
  letter-spacing: -2px;
  border-bottom: 1px solid #ccc;
  margin: 0 0 30px 0;
  padding: 0 0 25px 0;
}
.atom_slogan p {
	width: 70%;
	margin: 0 auto;
	color:#5a5a5a;
	font:14px 'Open Sans', sans-serif;
	line-height: 21px;
}
.atom_content {
	overflow: hidden;
	padding: 0 5px;
	margin: 0 0 75px 0;
}
.atom_box h3{
	display:inline-block;
	width: 67%;
	font: 18px 'Ubuntu Condensed', sans-serif;
	position:relative;
	z-index:1;
}
.atom_left_column .atom_box h3 {
	padding: 5px 0 5px 10px;
	box-shadow: -5px -5px 0 0 #d9d9d9;
}
.atom_right_column .atom_box h3 {
	padding: 5px 10px 5px 0;
	box-shadow: 5px -5px 0 0 #d9d9d9;
}
.atom_center_column {
	position: relative;
	z-index: 2;
}
.atom_box h3:before {
	content:"";
	position:absolute;
	top: 0;
    height: 100%;
    width: 20%;
	background:inherit;
	box-shadow: 2px -12px 0px -7px #d9d9d9;
}
.atom_left_column .atom_box h3:before {
	left: 88%;
	transform: skew(20deg, 0deg);
}
.atom_right_column .atom_box h3:before {
	left: -3%;
	transform: skew(-20deg, 0deg);
	box-shadow: -3px -12px 0px -7px #d9d9d9;
}
.atom_left_column .atom_box.social h3:before {
    left: 84%;
}
.atom_left_column .atom_box.social h3 {
    width: 61%;
}
.atom_right_column .atom_box.shop h3:before {
	left: -3%;
}
.atom_right_column .atom_box.shop h3 {
    width: 56%;
}
.atom_box.social {
	margin: 138px 15px 0 0;
}
.atom_box.shop {
	margin: 138px 0px 0 15px;
}
.atom_box_text {
	position:relative;
	margin: -15px 0 0 0;
    z-index: 0;
	border:1px solid #dcc1c9;
	border-left:none;
	border-right:none;
	padding: 20px 0 0px 0;
	height:80px;
	box-sizing:border-box;
}
.atom_box_text p{
	font: 12px 'Open Sans', sans-serif;
	color:#7f8081;
}
.atom_box_text:before {
	content:"";
	position:absolute;
    height: 55.6px;
    width: 55.6px;
	border:1px solid #dcc1c9;
	border-top:none;
	border-left:none;
}
.atom_left_column .atom_box_text:before {
	right: -28px;
    top: 11px;
	transform:rotate(-45deg);
}
.atom_right_column .atom_box_text:before {
    left: -28px;
    top: 11px;
    transform: rotate(135deg);
}
.atom_box a {
	font: 14px 'Bree Serif', sans-serif;
}
.atom .social a {
	color: #cb5a7c;
}
.atom_box.desing h3{
	background:#79769d;
	color: #f2f1ff;
}
.atom_box.desing a {
	color:#79769d;
}
.atom_box.social h3{
	background:#cb5a7c;
	color: #ffe3eb;
}
.atom_box.social a {
	color:#cb5a7c;
}
.atom_box.search h3{
	background:#dd574b;
	color: #f8e5e4;
}
.atom_box.search a {
	color:#dd574b;
}
.atom_box.shop h3{
	background:#8da552;
	color: #e9eedf;
}
.atom_box.shop a {
	color:#8da552;
}
.atom_more {
	display:inline-block;
	background:#dd574b;
	color:#ffebca;
	font: 22px 'Open Sans', sans-serif;
	padding: 10px 40px;
	border-radius:5px;
	letter-spacing: -1px;
	transition: 0.5s ease;
}
.portfolio {
	background:#2e3e4f;
	padding: 1%;
	text-align:justify;
	line-height: 0;
}
.portfolio_wraper {
	margin: 40px auto 55px;
}
.portfolio_title:after, .portfolio_content:after {
	width: 100%;
	height: 0;
	visibility: hidden;
	overflow: hidden;
	content: '';
	display: inline-block;
}
.portfolio h2 {
	color:#dd574b;
	font: 35px 'Bree Serif', sans-serif;
	display: inline-block;
}
.portfolio_more {
	display:inline-block;
	font: 20px 'Ubuntu Condensed', sans-serif;
	color: #fff;
	background: #dd574b;
	padding: 10px 25px;
	border-radius: 5px;
	transition: 0.5s ease;
}
.portfolio_content {
	margin: 55px 0 0 0;
	text-align:justify;
}
.portfolio_content_box {
	width: 23%;
	display: inline-block;
}
.portfolio_content_box a {
	display: block;
	position:relative;
	padding: 10px;
    background: #fff;
	overflow:hidden;
	margin: 0 0 15px 0;
}
.portfolio_content_box a:before {
	content:'';
	position:absolute;
	top: 0;
	background: rgba(31, 14, 14, 0.46);
	height:100%;
	width:100%;
	left:-100%;
	transition: 0.5s ease;
}
.portfolio_content_box a:hover .portfolio_content_box_info, .portfolio_content_box a:hover:before {
	left:0;
	transition: 0.5s ease;
}
.portfolio_content_box  img {
	width:100%;
}
.portfolio_content_box_info {
	position:absolute;
	width:100%;
    top: 0;
    left: -100%;
	transition: 0.5s ease;
	text-align:center;
    top: 50%;
    transform: translate(0%, -50%);
}
.portfolio_content_box:last-child {
	margin:0;
}
.portfolio_content_box h3 {
	color:#f98561;
	font: 17.5px 'Bree Serif', serif;
	margin: 0 0 5px 0;
}
.portfolio_content_box p {
	color:#cfd5d7;
	font: 12px 'Open Sans', sans-serif;
	text-align: left;
}
.portfolio_content_box_info p {
	text-align:center;
}
.portfolio_content_box span {
    color: #fbfbfb;
    font: 16px 'Open Sans', sans-serif;
    display: inline-block;
    padding: 10px 20px 10px 70px;
    border: 1px solid #fbfbfb;
    border-radius: 5px;
    margin: 20px 0 0 0;
    background: url(../images/icon-eye.png) no-repeat 15px center
}
.testimonials {
	text-align:center;
	background: #fbfbfb;
}
.testimonials_wraper {
	padding: 17px 0 33px 0;
}
.testimonials h2 {
	color: #446f9b;
	font: 32px 'bebas_neuebold', sans-serif;
	border-bottom:1px solid #8ec5de;
	text-transform: uppercase;
	margin: 0 0 46px 0;
	padding: 0 0 10px 0;
}
.testimonials_slogan {
	color: #515862;
    font: 25px 'Bree Serif', serif;;
	width: 67%;
	margin: 0 auto;
}
.testimonials_content{
	margin: 45px 0 0 0;
}
.testimonials_content_title {
	color:#f98561;
	font: 14px 'Open Sans', sans-serif;
	margin: 0 0 25px 0;
}
.footer {
    text-align: center;
    background: #2e3e4f;
	font-family:'Open Sans',sans-serif;
}
.footer_connect {
	background:#f2f2f2;
	padding: 50px 0 45px 0;
	overflow: hidden;
	margin: 0 0 15px 0;
}
.footer_connect p {
	color:#2e3e4f;
	font-size: 32px;
	letter-spacing: -2px;
	font-weight: 300;
}
.footer_connect  .testimonials_get{
	display:inline-block;
	padding: 10px 60px;
	border-radius:5px;
	background:#dd574b;
	color:#ffebca;
	font-size: 24px;
	font-weight: 300;
	letter-spacing: -1px;
	margin: 25px 0 0 0;
	transition:0.5s ease;
}
.footer_menu {
	display:inline-block;
	padding: 10px 20px;
	border-radius: 0 0 5px 5px;
}
.footer_menu li {
	display:inline-block;
	padding: 0 5px 0 28px;
}
.footer_menu li:first-child {
	padding-left:0;
}
.footer_menu a {
	text-decoration:none;
	font-size: 15px;
	color:#808b96;
}
.footer_menu a:hover {
	color:#fbfbfb;
	transition:0.5s ease;
}
.footer_menu .active {
	color:#4f97e9;
}
.footer_social_menu{
	display:inline-block;
	padding: 15px 20px;
}
.footer_social_menu li {
	display:inline-block;
	padding: 0 3px 0 15px;
}
.footer_social_menu a {
	display:inline-block;
	width:45px;
	height:45px;
	transition:0.2s ease;
}
.footer_social_menu a:hover {
	transform:scale(1.1);
	transition:0.2s ease;
}
.footer_social_menu li:first-child {
	padding-left:0;
}
.footer_social_menu_fb {
	background:url(../images/social_footer.png) no-repeat -3px -3px;
}
.footer_social_menu_tw {
	background:url(../images/social_footer.png) no-repeat -65px -3px;
}
.footer_social_menu_be {
	background:url(../images/social_footer.png) no-repeat -133px -3px;
}
.footer_social_menu_in {
	background: url(../images/social_footer.png) no-repeat -195px -3px;
}
.footer_copyright {
	color:#bbc1c9;
	font-size: 13px;
	padding: 15px 0 20px 0px;
}
